import { Button, Card, Container, Input, Row, Text } from "@nextui-org/react";
import React, { FC, useCallback, useRef } from "react";
import { DEFAULT_PARSER_CONFIG } from "../config";
import { ParserConfig, Stage, StageProps } from "../models";

interface Props extends StageProps {
  fileContent: string;
  savedConfigs: ParserConfig[];
  setFileContent: (value: string) => void;
  parseContent: (config: ParserConfig) => void;
}

export const FileInputStage: FC<Props> = (props) => {
  const { fileContent, savedConfigs, setFileContent, setStage, parseContent } =
    props;

  const fileInputRef = useRef<HTMLInputElement>(null);

  const readFile = useCallback(() => {
    const file = fileInputRef.current?.files?.[0];

    if (!file) {
      return;
    }

    const reader = new FileReader();
    reader.readAsText(file, "UTF-8");
    reader.onload = (e) => {
      const fileContent = e?.target?.result;

      if (fileContent) {
        setFileContent(fileContent as string);
      }
    };
  }, [setFileContent]);

  const handleDefaultParser = useCallback(
    () => parseContent(DEFAULT_PARSER_CONFIG),
    [parseContent]
  );

  const handleCustomParser = useCallback(() => {
    if (fileContent.length === 0) {
      return;
    }

    setStage(Stage.CUSTOMIZE);
  }, [fileContent, setStage]);

  return (
    <Row
      style={{
        width: "100%",
        height: "100vh",
        flex: 1,
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
      }}
      css={{ background: "$accents1" }}
    >
      <Container style={{ maxWidth: 800 }}>
        <Card css={{ p: "$16" }}>
          <Card.Body css={{ display: "flex", justifyContent: "center" }}>
            <Text h3 css={{ textAlign: "center", pb: "$8" }}>
              Select file to parse
            </Text>
            <Input
              aria-label="file"
              shadow
              type="file"
              ref={fileInputRef}
              onChange={readFile}
            />
            {fileContent.length > 0 && (
              <>
                <Text h3 css={{ textAlign: "center", pt: "$8" }}>
                  Select config
                </Text>
                <Row justify="center" css={{ pt: "$8" }}>
                  <Button auto shadow onPress={handleDefaultParser}>
                    Default
                  </Button>
                  {savedConfigs.map((config, index) => (
                    <Button
                      key={config.name}
                      shadow
                      auto
                      color="secondary"
                      onPress={() => parseContent(config)}
                      css={{ ml: "$8" }}
                    >
                      {config.name}
                    </Button>
                  ))}
                </Row>
                <Row justify="center" css={{ pt: "$12" }}>
                  <Button shadow color="gradient" onPress={handleCustomParser}>
                    Create custom config
                  </Button>
                </Row>
              </>
            )}
          </Card.Body>
        </Card>
      </Container>
    </Row>
  );
};
