import { useCallback, useEffect, useState } from "react";
import toast from "react-hot-toast";
import { ParserConfig } from "./models";

export const useSavedConfigs = () => {
  const [savedConfigs, setSavedConfigs] = useState<ParserConfig[]>(() => {
    try {
      const data = localStorage.getItem("parser_configs");
      if (!data) {
        return [];
      }
      return JSON.parse(data) as unknown as ParserConfig[];
    } catch {
      return [];
    }
  });

  const saveConfig = useCallback(
    (config: ParserConfig) => {
      if (
        savedConfigs.findIndex((item) => item.name === config.name) !== -1 ||
        config.name === "default"
      ) {
        toast.error("Config with the same name already exists");

        return false;
      }

      setSavedConfigs((s) => [...s, config]);

      toast.success("Config has been saved!");

      return true;
    },
    [savedConfigs]
  );

  const removeConfig = useCallback((config: ParserConfig) => {
    setSavedConfigs((s) => s.filter((item) => item.name !== config.name));

    toast.success("Config has been removed!");
  }, []);

  useEffect(() => {
    localStorage.setItem("parser_configs", JSON.stringify(savedConfigs));
  }, [savedConfigs]);

  return { savedConfigs, saveConfig, removeConfig };
};
