import {
  Button,
  Card,
  Container,
  Input,
  Link,
  Modal,
  Row,
  Text,
} from "@nextui-org/react";
import React, { FC, useCallback, useState } from "react";
import toast from "react-hot-toast";
import { ParserConfig, ResultItem, StageProps } from "../models";

interface Props extends StageProps {
  result: ResultItem[];
  config: ParserConfig;
  savedConfigs: ParserConfig[];
  saveConfig: (config: ParserConfig) => boolean;
  removeConfig: (config: ParserConfig) => void;
  resetApp: () => void;
}

export const ResultStage: FC<Props> = (props) => {
  const { result, config, savedConfigs, saveConfig, removeConfig, resetApp } =
    props;

  const isNotDefaultConfig = config.name !== "default";

  const isSavedConfig =
    savedConfigs.findIndex((item) => item.name === config.name) !== -1;

  const [savingVisible, setSavingVisible] = useState(false);
  const [configName, setConfigName] = useState("");

  const copyText = useCallback((text: string) => {
    navigator.clipboard.writeText(text).then(
      () => {
        toast.success("Copied!", { duration: 2000 });
      },
      (err) => {
        console.error("Async: Could not copy text: ", err);
      }
    );
  }, []);

  return (
    <Container css={{ p: "$8" }}>
      <Row justify="space-between">
        <Link css={{ mb: "$8" }} onClick={resetApp}>
          ← Back
        </Link>
        {isNotDefaultConfig && (
          <>
            {isSavedConfig ? (
              <Link onClick={() => removeConfig(config)}>Delete config</Link>
            ) : (
              <Link onClick={() => setSavingVisible(true)}>Save config</Link>
            )}
          </>
        )}
      </Row>
      {result.map((item, index) => (
        <Card key={index} css={{ mb: "$8" }}>
          <div style={{ position: "absolute", top: 0, right: 16 }}>
            <Text h1 css={{ color: "$accents4" }}>
              #{index + 1}
            </Text>
          </div>
          <Card.Body css={{ display: "flex", justifyContent: "center" }}>
            {item.cells.map((text) => (
              <React.Fragment key={text}>
                <pre
                  style={{
                    margin: 0,
                    padding: 5,
                    overflow: "visible",
                    cursor: "pointer",
                  }}
                  onClick={() => copyText(text)}
                >
                  {text.trim()}
                </pre>
              </React.Fragment>
            ))}
          </Card.Body>
        </Card>
      ))}
      <Modal
        closeButton
        aria-labelledby="modal-title"
        open={savingVisible}
        onClose={() => setSavingVisible(false)}
      >
        <Modal.Header>
          <Text id="modal-title" size={18}>
            Enter name
          </Text>
        </Modal.Header>
        <Modal.Body>
          <Input
            aria-label="config name"
            clearable
            bordered
            fullWidth
            color="primary"
            size="lg"
            placeholder="Config name"
            value={configName}
            onChange={(e) => setConfigName(e.target.value)}
          />
        </Modal.Body>
        <Modal.Footer justify="center">
          <Button
            disabled={configName.length === 0}
            onPress={() => {
              const saved = saveConfig({ ...config, name: configName });
              if (saved) {
                config.name = configName;
                setSavingVisible(false);
              }
            }}
          >
            Save
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
};
