import React, { FC, useCallback } from "react";
import { NextUIProvider } from "@nextui-org/react";
import "./App.css";
import { FileInputStage } from "../FileInputStage/FileInputStage";
import { useState } from "react";
import { ParserConfig, ResultItem, Stage } from "../models";
import { ResultStage } from "../ResultStage/ResultStage";
import toast, { Toaster } from "react-hot-toast";
import { CustomizeStage } from "../CustomizeStage/CustomizeStage";
import { DEFAULT_PARSER_CONFIG } from "../config";
import { useSavedConfigs } from "../useSavedConfigs";

const App: FC = () => {
  const [stage, setStage] = useState(Stage.FILE_INPUT);

  const [fileContent, setFileContent] = useState("");
  const [result, setResult] = useState<ResultItem[] | null>(null);

  const [lastUsedConfig, setLastUsedConfig] = useState(DEFAULT_PARSER_CONFIG);

  const { savedConfigs, saveConfig, removeConfig } = useSavedConfigs();

  const parseContent = useCallback(
    (config: ParserConfig) => {
      if (fileContent.length === 0) {
        return;
      }

      const result = fileContent
        .split("\n")
        .filter((item) => item.includes(config.divider))
        .map((item) => {
          const cells = item.split(new RegExp(`${config.divider}(?!\\s)`));

          return { cells: config.indexes.map((index) => cells[index]) };
        })
        .filter((item) => item.cells.every(Boolean));

      if (result.length === 0) {
        toast.error("Nothing to show");
        return;
      }

      setLastUsedConfig(config);
      setResult(result);
      setStage(Stage.RESULT);
    },
    [fileContent]
  );

  const resetApp = useCallback(() => {
    setStage(Stage.FILE_INPUT);
    setResult(null);
  }, []);

  return (
    <NextUIProvider>
      {stage === Stage.FILE_INPUT && (
        <FileInputStage
          savedConfigs={savedConfigs}
          setStage={setStage}
          fileContent={fileContent}
          setFileContent={setFileContent}
          parseContent={parseContent}
        />
      )}
      {stage === Stage.RESULT && result && (
        <ResultStage
          setStage={setStage}
          result={result}
          resetApp={resetApp}
          config={lastUsedConfig}
          savedConfigs={savedConfigs}
          saveConfig={saveConfig}
          removeConfig={removeConfig}
        />
      )}
      {stage === Stage.CUSTOMIZE && (
        <CustomizeStage
          setStage={setStage}
          fileContent={fileContent}
          resetApp={resetApp}
          parseContent={parseContent}
        />
      )}
      <Toaster position="top-right" />
    </NextUIProvider>
  );
};

export default App;
