import {
  Button,
  Card,
  Checkbox,
  Container,
  Link,
  Row,
  Text,
} from "@nextui-org/react";
import { FC, useCallback, useEffect, useMemo, useState } from "react";
import { ParserConfig, StageProps } from "../models";

interface Props extends StageProps {
  fileContent: string;
  parseContent: (config: ParserConfig) => void;
  resetApp: () => void;
}

export const CustomizeStage: FC<Props> = (props) => {
  const { fileContent, parseContent, resetApp } = props;

  const [divider, setDivider] = useState("");

  const [selectedIndexes, setSelectedIndexes] = useState<number[]>([]);

  const fields = useMemo(() => {
    if (!divider) {
      return [];
    }

    const items = fileContent
      .split("\n")
      .filter((item) => item.includes(divider));

    const exampleItem = items[items.length > 2 ? 2 : 0];

    return exampleItem
      .split(new RegExp(`${divider}(?!\\s)`))
      .map((field, index) => ({
        value: field,
        selected: selectedIndexes.includes(index),
      }));
  }, [divider, fileContent, selectedIndexes]);

  const handleSelect = useCallback((index: number) => {
    setSelectedIndexes((s) => {
      if (s.includes(index)) {
        return s.filter((item) => item !== index);
      }

      return [...s, index];
    });
  }, []);

  const handleShowResult = useCallback(() => {
    parseContent({ name: "", divider, indexes: selectedIndexes });
  }, [divider, parseContent, selectedIndexes]);

  useEffect(() => {
    if (!divider) {
      return;
    }

    setSelectedIndexes([]);
  }, [divider]);

  return (
    <Row
      style={{
        width: "100%",
        minHeight: "100vh",
        flex: 1,
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
      }}
      css={{ background: "$accents1" }}
    >
      <Container style={{ maxWidth: 800 }}>
        <Link onClick={resetApp}>← Back</Link>
        <Card css={{ p: "$8", m: "$8" }}>
          <Card.Body>
            <Text h3 css={{ textAlign: "center", pb: "$16" }}>
              Select divider
            </Text>
            <Row justify="center">
              <Button shadow css={{ mr: "$8" }} onPress={() => setDivider("|")}>
                |
              </Button>
              <Button shadow color="secondary" onPress={() => setDivider(";")}>
                ;
              </Button>
            </Row>
            {fields.length > 0 && (
              <>
                <Text h3 css={{ textAlign: "center", pt: "$16" }}>
                  Select fields
                </Text>
                {fields.slice(0, 20).map((field, index) => (
                  <Checkbox
                    key={`${field.value}_${index}`}
                    isSelected={field.selected}
                    onChange={() => handleSelect(index)}
                  >
                    {field.value.slice(0, 50)}
                    {field.value.length > 50 && "..."}
                  </Checkbox>
                ))}
                <Row justify="center" css={{ pt: "$16" }}>
                  <Button
                    shadow
                    disabled={selectedIndexes.length === 0}
                    onPress={handleShowResult}
                  >
                    Show result
                  </Button>
                </Row>
              </>
            )}
          </Card.Body>
        </Card>
      </Container>
    </Row>
  );
};
